<template>
    <div class="father">
    <mt-loadmore 
        :top-method="loadTop" 
        :bottom-method="loadBottom"
        :bottom-all-loaded="allLoaded" 
        ref="loadmore"
        >
     <ul
     v-infinite-scroll="loadMore"
            infinite-scroll-disabled="loading"
            infinite-scroll-distance="100"
     >
         <li v-for="(good,i) in goods" :key="i" class="moveIn">
             
             <router-link :to="{name:'goodDetail',params:{goodId:good._id}}">
 <img :src="good.goodsimg">
 <p class="name">{{good.goodsname}}</p>
 <p class="price">￥:{{good.goodsprice}}RMB</p>
 
             </router-link>
            <button class="buy" @click.stop="gotobuy(good._id)">立即购买</button>
         </li>
     </ul>
        </mt-loadmore>  
        <div class="goodone" v-show="show">
            <div class="header">
        <img :src="good.goodsimg" class="avt">
        <p class="jiage">￥{{good.goodsprice}}</p>
        <p class="prop" >已选:{{good.goodsname}}</p>
        <i class="close" @click="close">关闭</i>
        </div>
        <div class="xiangqing">
            <p>{{good.goodsshuoming}}</p>
        </div>
        <div class="baozhang">
            <p class="fuwu">保障服务</p>
            <p class="xihu">洗护保障</p>
        </div>
        <div class="btns">
            <div class="btn" @click="buy(good)">确认</div>
        </div>
        
        </div>
    </div>
</template>


<script>
import {Toast} from "mint-ui";
import {http} from "../utils";
import {Indicator} from "mint-ui";
import { setTimeout } from 'timers';
export default {
    data(){
        return{
           goods:[],
            allLoaded:false,
            loading:false,
            good:[],
            show:false
        }
    },
     props:["type","goodsList"],
     methods:{
           loadTop(){
            console.log("下拉刷新 --top")
            setTimeout(()=>{
                this.goods.reverse();
              
                this.$refs.loadmore.onBottomLoaded();
                Toast({
                    message:'刷新成功',
                    duration:500
                });
            },1000)
        },
        loadBottom(){
         
        },
        loadMore(){
           
            
        },
        close(){
            this.show = false;
        },
        gotobuy(id){
           this.show = true;
            http.get("/vue/getGoodOne",{
                params:{
                    _id:id
                }
            }).then(res=>{
             console.log(res);
             this.good = res.data.result;
           
            })
        },
        buy(good){
            console.log(good)
            if(localStorage.username){
                 Indicator.open({
            text: '正在购买中...',
            spinnerType: 'triple-bounce'
        });
                http.get("/vue/gotoshppinghouse",{
                    params:{
                        username:localStorage.username,
                         
          goodsimg:good.goodsimg,
          goodsid:good._id,
          goodsname:good.goodsname,
          goodsprice:good.goodsprice,
          goodsnum:1,
          goodstype:good.goodstype,
                    }
                }).then(res=>{
                    
                    Indicator.close();
                     Toast("提交订单成功,请付款");
                      this.show = false;
                    
                      this.$router.push({name:"dingdan"})
                })
            }else{
                 Toast("请先进行登录噢");
                this.$router.push({name:"login"});
            }
               
            

        }
      
     },

    
     mounted(){
        
     setTimeout(()=>{
         this.goods = this.goodsList.filter((good)=>good.goodstype==this.type)
          console.log(this.goods);
     },2000)
      console.log(this.goods);
        
     
     }
}
</script>


<style scoped>
ul, img{
    width: 100%;
    text-align: center;
}
ul{
    overflow: hidden;
}
ul li{
    width: 50%;
    float: left;
    height: 100%;
}
ul{
    padding-top:0.2rem;
    padding-bottom: .9rem;
}
.name{
    color: #000000;
}
.price{
    color: red;
    font-size: .26rem;
    font-weight: 700;
}
.moveIn{
    animation:rotateIn 1s ; 
}
.buy{
    display: inline-block;
    width: 1.6rem;
    height: .5rem;
    background-color: #000000;
    color: white;
    text-align: center;
    line-height: .5rem;
    margin: .2rem auto;
    border: none;
}
.goodone{
    position: fixed;
    width: 100%;
   
    left: 0;
    bottom:.9rem;
    background-color:white;
}
.header{
    height: 70px;
    padding: 0 0 10px 110px;
    background-color: #fff;
    position: relative;
}
 .avt {
    position: absolute;
    left: .1rem;
    top: -.3rem;
    border-radius: 2px;
    width: 1.9rem;
    height: 1.9rem;
}
.header .jiage {
    display: inline-block;
    height: 40px;
    line-height: 40px;
    color: #e4393c;
    font-size:.4rem;
    font-weight: 700;
}
 .header .prop {
    word-break: break-all;
    font-size: .3rem;
    color: #333;
    line-height: 1.4em;
    padding-right: .2rem;
}
 .header .close {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 1.5rem;
    height: .3rem;
    padding: 10px;
   text-align: center;
   font-style: normal;
}
.xiangqing,.baozhang{
   margin: .4rem;
}
.fuwu{
    color: gray;
    font-size: .2rem;
}
 .btns {
    position: absolute;
    bottom:0;
    left: 0;
    width: 100%;
    height: 1rem;
    background-color: #e4393c
}
 .btn {
    height: 1rem;
    line-height: 1rem;
    color: #fff;
    font-size: 16px;
    text-align: center;
}
</style>
