<template>
<div>
     <Head :title="good.goodsname" :show="!!1"></Head>
     <div class="goodsdetail">
        <p class="name">{{good.goodsname}}</p>
        <img :src="good.goodsimg">
         <p class="color">立即试色</p>

     </div>
     <div class="xiangqing">
         <p class="type">{{good.goodstype}}</p>
         <p class="goodsname">{{good.goodsname}}</p>
        
         <p class="price">￥:{{good.goodsprice}}</p>
         <p class="shuliang"><button @click="num--" class="jian">-</button><input v-model="num" class="num"><button class="jia" @click="num++">+</button></p>
         <p class="xiangqing">产品详情:</p>
         <p class="shuoming">{{good.goodsshuoming}}</p>
         <img :src="good.goodsshuomingimg" class="img2">
     </div>
     <div class="fixed">
         <button @click="shoucang()" class="shoucang"><span class="iconfont">&#xe60a;</span>收藏</button>
         <button @click="gotocar()" class="car">加入购物车</button>
         <button @click="gotobuy()" class="buy">立即购买</button>
     </div>
</div>
    
</template>


<script>
import {Indicator} from "mint-ui";
import {http} from "../utils";
import { Toast } from "mint-ui";
export default {
    data(){
        return {
            good:{},
            num:1
        }
    },
    methods:{
       gotocar(id){
            if(localStorage.username){
                  Indicator.open({
            text: '正在添加中...',
            spinnerType: 'triple-bounce'
        });
               http.get("/vue/gotocar",{
                   params:{
                       username:localStorage.username,
                        goodsid:this.good._id,
                        goodsimg:this.good.goodsimg,
                        goodsnum:this.num,
                        goodsname:this.good.goodsname,
                        goodsprice:this.good.goodsprice,
                          goodstype:this.good.goodstype
                   }
                        
               }).then(res=>{
                   console.log(res)
                    Indicator.close();
                     Toast("添加成功");
               })
            }else{
                 Toast("请先进行登录噢");
                this.$router.push({name:"login"});
            }
       },
       gotobuy(){
           if(localStorage.username){
                 Indicator.open({
            text: '正在购买中...',
            spinnerType: 'triple-bounce'
        });
                http.get("/vue/gotoshppinghouse",{
                    params:{
                        username:localStorage.username,
                        goodsid:this.good._id,
                        goodsimg:this.good.goodsimg,
                        goodsnum:this.num,
                        goodsname:this.good.goodsname,
                        goodsprice:this.good.goodsprice,
                        goodstype:this.good.goodstype
                    }
                }).then(res=>{
                    console.log(res)
                    Indicator.close();
                     Toast("提交订单成功,请付款");
                      this.show = false;
                })
            }else{
                 Toast("请先进行登录噢");
                this.$router.push({name:"login"});
            }
       },
        shoucang(){
            if(localStorage.username){
                  Indicator.open({
            text: '正在收藏中...',
            spinnerType: 'triple-bounce'
        });
               http.get("/vue/gotoshoucangcar",{
                   params:{
                       goodsid:this.good._id,
                        goodsimg:this.good.goodsimg,
                        
                        goodsname:this.good.goodsname,
                        goodsprice:this.good.goodsprice,
                       username:localStorage.username
                      
                   }
               }).then(res=>{
                   console.log(res)
                   if(res.data.result.type=="1"){
                       Toast("该商品已经收藏过");
                         Indicator.close();
                   }else{
                        Indicator.close();
                     Toast("收藏成功");
                   }
                   
               })
            }else{
                 Toast("请先进行登录噢");
                this.$router.push({name:"login"});
            }
       },
    },
    mounted(){
        Indicator.open({
            text: '加载中...',
            spinnerType: 'triple-bounce'
        });
        console.log(this.$route);
        http.get("/vue/getGoodOne",{
            params:{
                _id:this.$route.params.goodId
            }
        }).then(res=>{
            console.log(res);
            this.good = res.data.result;
            Indicator.close();
        })

    }
}
</script>


<style scoped>
.goodsdetail{
    width: 100%;
    text-align: center;
    padding-top: .3rem;
}
img{
    display: block;
    margin: .1rem auto;
}
.name{
    font-size: 20px;
    font-weight: 700;
    margin-bottom: .3rem;
}
.color{
    width: 2.7rem;
    height: 1rem;
    background-color: #000;
    color: white;
    text-align: center;
    line-height:1rem;
    margin: .3rem auto;
}
.color:hover{
    color: #000;
    background-color:white;
}
.xiangqing p{
    margin-left: .3rem;
    line-height: .6rem;
    
}
.type{
    font-weight: 700;
    font-size: .4rem;
}
.goodsname{
    font-weight: 700;

}
.price{
    font-size: .4rem;

}
.xiangqing{
    width: 100%;
    border-bottom:1px solid #ccc;
}
.fixed{
    position: fixed;
    left: 0;
    bottom: 0rem;
    margin-top: 1.3rem;
}
.fixed button{
    width: 2rem;;
    height: .8rem;
   line-height: .8rem;
    color: white;
    float: left;
    border:none;   
}
.car{
    background: #ff9600;
}
.buy{
        background: #e4393c;
}
.shoucang{
    margin-left: .6rem;
     background: #e4393c;
}
.img2{
    width: 100%;
}
.shuliang{
    height: 1rem;
}
.jian,.jia{
    border: none;
    width: .8rem;
    height: .8rem;
    text-align: center;
    line-height: .4rem;
    float: left;
    background-color: cyan;
}
.jia{
    background-color: crimson;
}
.num{
    float: left;
    height:.8rem;
    box-sizing: border-box;
    width: .8rem;
    text-align: center
}
</style>
