<template>
    <div>
<Head title="搜索" :show="!!1"></Head>
      <mt-search v-model.lazy="value"></mt-search>  
     
     <ul>
         <li v-for="(good,i) in goodList" :key="i" class="moveIn">
             
             <router-link :to="{name:'goodDetail',params:{goodId:good._id}}">
 <img :src="good.goodsimg">
 <p class="name">{{good.goodsname}}</p>
 <p class="price">￥:{{good.goodsprice}}RMB</p>
             </router-link>
            
         </li>
     </ul>
       
    </div>
</template>


<script>
import {http} from "../utils"
import {Toast} from "mint-ui";

export default {
    data() {
        return {
            value:"",
           goodList:[]
        }
    },
    
    watch:{
        value(newVal){
           http.get("/vue/search",{
               params:{
                   keyword:newVal
               }
           }).then(res=>{
               this.goodList = res.data.result;
               Toast({
                   msg:"搜索成功",
                   duration:800,
                   icon:"iconfont icon goods_fill"
               })
           })
        }
    }
}
</script>
<style scoped>
.mint-search{
    height: 1rem;
}
ul li{
    width: 100%;
    margin: .2rem auto;
    text-align: center;
}
li img{
    display: inline;
    margin: .1rem auto;
}
.name{
    color: #000;
    font-weight: 700;
}
.price{
    color: red;
    font-weight: 700;
    
}
</style>
