<template>
    <div>
        <Head title="我的收藏" :show="!!1"></Head>
         <ul
     
     >
         <li v-for="(good,i) in goods" :key="i" class="moveIn">
             
             <router-link :to="{name:'goodDetail',params:{goodId:good._id}}">
 <img :src="good.goodsimg">
 <p class="name">{{good.goodsname}}</p>
 <p class="price">￥:{{good.goodsprice}}RMB </p>
   
             </router-link>
          
         </li>
     </ul>
    </div>
</template>

<script>
import { http } from '../utils';
export default {
    
   data(){
       return{
           goods:[]
       }
   },
    mounted(){
        http.get("/vue/getLikeGoods",{
            params:{
                username:localStorage.username
            }
        })

      .then(res=>{   
         
            this.goods = res.data.result;                                          
   
     })
     
    }
}
</script>

<style scoped>
ul, img{
    width: 100%;
    text-align: center;
    
}
ul{
    margin-top: 1.4rem;
      padding-bottom: 3.9rem;
      margin-bottom: 10rem;
}
.mint-loadmore{
       
      margin-bottom:.8rem;
}
li{
    width: 50%;
    float: left;
    height: 100%;
}
li{
    margin-bottom: .2rem;
}

.name{
    color: #000000;
}
.price{
    color: red;
    font-size: .4rem;
    font-weight: 700;
}
.moveIn{
    animation:bounceln 2s ;
        height: 6rem; 
}
.iconfont{
    color: aqua;
    font-size: .4rem;
}
.xiangqing{
    margin-left: .3rem;
    margin-right: .3rem;
}
</style>
