<template>
    <div>
        <Head title="购物车"></Head>

        <div v-if="show">
            <img :src="img" class="goodcarimg"  >
        </div>
        <div v-else>
       <ul>
         <li class="lb" v-for="(good,index) in goods" :key="index">
        <div class="check">
            <input type="checkbox" v-model="good.check">
        </div>
         <router-link :to="{name:'goodDetail',params:{goodId:good.goodsid}}">
        <img :src="good.goodsimg" class="spt">
            </router-link>
        <div class="mid">
            <p class="name">
                {{good.goodsname}}
            </p>
            <p class="color">类型:{{good.goodstype}}</p>
            <div class="jj">
                <span class="jian"  @click="updategood(good,--good.goodsnum)">-</span>
                    <input type="number" class="num" v-model="good.goodsnum" min="1" @change="updategood(good,goodsnum)">
                <span class="jia" @click="updategood(good,++good.goodsnum)">+</span>
            </div>
            
        </div>
            <div class="jgq">
                <div class="price">
                    <p>单￥:{{good.goodsprice}}</p>
                    <p>总￥：{{good.goodsprice*good.goodsnum}}</p>
                </div>
                <span class="del iconfont" @click="delGood(good.goodsid,good.username,good._id)">&#xe612;</span>
            </div>
            </li>
                
        </ul>
       <div class=fix_bottom>
           <div id="qx">
               <input type="checkbox"  v-model="all">
           </div>
           <span class="all">全选</span>
           <div class="zjq">
               <p class="p1">
                   合计:￥:{{totalPrice}}
               </p>
           </div>
           <a id="js" @click="jiesuan()">
               结算<span class="zsl">￥:{{totalPrice}}</span>
           </a>
       </div>
        </div>
    </div>
</template>


<script>
import { http } from '../utils';
import { MessageBox } from 'mint-ui'
import { Toast } from "mint-ui";
import {Indicator} from "mint-ui";
import { truncateSync } from 'fs';
import {mapActions} from "vuex";
import { setTimeout } from 'timers';

export default {
    data(){
        return{
            show:false,
            goods:[],
            check:false,
           num:"",
           img:require("../assets/images/goodscar.png"),
        }
    },
    methods:{
        ...mapActions(["update","jie","dingdan"]),
    delGood(id,username,_id){
        MessageBox.confirm('你确定删除该商品吗?').then(action => {
             Indicator.open({
            text: '正在忍痛删除中...',
            spinnerType: 'triple-bounce'
        });
            http.get("/vue/deleteGoods",{
                params:{
                    goodsid:id,
                    username:username
                }
            }).then(res=>{
                console.log(res);
                 Indicator.close();
                Toast("删除成功")
            this.goods = this.goods.filter((good)=>good._id!=_id);
                // this.$router.go()
            })
});
    },
    updategood(good,goodsnum){
         Indicator.open({
            
            spinnerType: 'triple-bounce'
        });
        if(goodsnum<1){
           goodsnum=1;
        }
        this.update({
            url:"/vue/updateGood",
            id:good._id,
            goodsnum:goodsnum,
            cb:()=>{
                  Indicator.close();
               
                // this.$router.go()
            }

        })
    },
    jiesuan(){
        
         Indicator.open({
            text: '正在结算中...',
            spinnerType: 'triple-bounce'
        });
        var list = [];
        var list2 = [];
        this.goods.forEach((good)=>{
            if(good.check){
                list.push(good._id);
                list2.push(good);
               
            }
        })
         for(var i =0;i<list2.length;i++){
                    var goodone = list2[i]
                }
        console.log(goodone);
        // console.log(list2);
        // console.log(list2)
         setTimeout(() => {
                 this.jie({
           url:"/vue/jiesuanGood",
           list:list,

           cb:()=>{
                Indicator.close();
                
           
           
                  setTimeout(()=>{
                //    this.$router.go()
                    },2500)
           }
       })
           }, 1000);
     
      this.dingdan({
          url:"/vue/gotoshppinghouse",
         username:goodone.username,
          goodsimg:goodone.goodsimg,
          goodsid:goodone.goodsid,
          goodsname:goodone.goodsname,
          goodsprice:goodone.goodsprice,
          goodsnum:goodone.goodsnum,
          goodstype:goodone.goodstype,
          cb:()=>{
               Toast("购买成功,马上给您安排发货")
               this.$router.push({name:"dingdan"})
          }
      })  
    }
    },
    mounted(){
       if(!localStorage.username){
           this.$router.replace({name:"login"})
       }else{
          
            http.get("/vue/goodsnum",{
			params:{
				username:localStorage.username
			}
		}).then(res=>{
			// console.log(res);
            this.num = res.data.num*1;
            if(this.num>0){
                this.show=false
            }else{
                this.show = true;
            }
		})
     http.get("/vue/getgoodsche",{
         params:{
            username:localStorage.username
         }
     }).then(res=>{
    //    console.log(res)
       this.goods = res.data.result
     })
       }
        
    },
   computed:{
       totalPrice:{
            get(){
                var totalPrice = 0;
                this.goods.forEach((good)=>{
                    if(good.check){
                        totalPrice+=good.goodsprice*good.goodsnum*1;
                    }                             
            })
            return totalPrice;
            }
       },
       all:{
           get(){
               var flag = false;
               this.goods.forEach((good)=>{
                   if(good.check){
                       flag = true;
                   }
               })
               return flag;
           },
            set(newVal) {
                        this.goods = this.goods.map((good) => {
                            good.check = newVal;
                            return good;
                        })

                    }
           
       }
   }
    
}

</script>


<style scoped>
*{
    margin: 0;
    padding: 0;
}
.goodcarimg{
    width: 100%;
}
 .lb {
    display: block;
    width: 100%;
    height: 2rem;
    margin-bottom: 0.3rem;
    border-bottom: 1px solid #ccc;
    background: #fafafa;
}
.lb .check {
    width: 0.7rem;
    height: 2rem;
    float: left;
    text-align: center;
    line-height: 2rem;
}
.lb .spt {
    display: block;
    width: 1.36rem;
    height: 1.4rem;
    float: left;
    margin-top: 0.26rem;
    background-color: aqua
}
.lb .mid {
    width: 2.52rem;
    height: 1.7rem;
    float: left;
    margin-left: 0.18rem;
    margin-top: 0.16rem;
}
.lb .mid .name {
    font-size: 0.28rem;
    color: #333;
    text-overflow: -o-ellipsis-lastline;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    padding-top: 0.1rem;
    width: 3rem;
}
.lb .mid .color {
    font-size: 0.22rem;
    color: #8a8a8a;
    display: block;
    margin-top: 0.1rem;
}
.lb .mid .jj {
    width: 1.52rem;
    height: 0.4rem;
    border: 1px solid #dcdcdc;
    border-radius: 2px;
    margin-top: 0.1rem;
}
.lb .mid .jj .jian {
    display: block;
    font-size: 0.4rem;
    text-align: center;
    line-height: 0.4rem;
    width: 0.49rem;
    height: 0.38rem;
    float: left;
    box-sizing: border-box;
}
.lb .mid .jj .num {
    display: block;
    float: left;
    text-align: center;
    line-height: 0.4rem;
    width: 0.48rem;
    height: 0.35rem;
    font-size: 0.24rem;
    border-left: 1px solid #dcdcdc;
    border-right: 1px solid #dcdcdc;
     box-sizing: border-box;
     border: 0;
}
.lb .mid .jj .jia {
    display: block;
    font-size: 0.28rem;
    text-align: center;
    line-height: 0.4rem;
    width: 0.49rem;
    height: 0.38rem;
    float:left;
     box-sizing: border-box;
}
.lb .jgq {
    float: right;
    margin-right: 0.18rem;
    width: 1.44rem;
    height: 2rem;
    position: relative;
}
.lb .jgq .price {
    width: 100%;
    height: 0.34rem;
    font-size: 0.24rem;
    font-weight: bold;
    color: #e90327;
    display: block;
    float: right;
    margin-top: 0.26rem;
}
.lb .jgq .del {
    display: block;
   
    height: 0.46rem;
    float: right;
    margin-bottom: 0.4rem;
    position: absolute;
    bottom: -0.2rem;
    /* left: 0.8rem; */
    right: 0;
}
.fix_bottom {
    position: fixed;
    width: 100%;
    height: 0.8rem;
    background: #fafafa;
    bottom:1rem;
    font-size: 0.24rem;
    color: #333;
}
#qx {
    width: 0.4rem;
    height: 0.4rem;
    margin-top: 0.8rem;
    margin-left: 0.2rem;
    display: block;
   
    background-size: 0.4rem 0.4rem;
    float: left;
    margin-left: 0.2rem;
    margin-top: 0.2rem;
    line-height: 0.8rem;
}
.all{
    float:left;line-height:0.8rem;
}
.fix_bottom .zjq {
    float: left;
    width: 1.96rem;
    height: 0.8rem;
    margin-left: 0.7rem;
}
.fix_bottom .zjq .p1 {
    font-size: 0.26rem;
    line-height: 0.4rem;
    font-weight: bold;
    color: red;
}
#js {
    display: block;
    width: 2.5rem;
    height: 0.8rem;
    background: #e90327;
    color: #fff;
    font-size: 0.28rem;
    font-weight: bold;
    text-align: center;
    line-height: 0.8rem;
    float: right;
    text-decoration: none;
}

</style>
