<template>
    <div>
        <mt-header :title="title" id="ysl">
  <router-link to="/" slot="left" v-if="show">
    <mt-button icon="back" @click="goback">返回</mt-button>
  </router-link>
  <mt-button icon="search" slot="right" @click="gotoserach" v-if="!flag"></mt-button>
</mt-header>
        </div>
</template>


<script>
export default {

props:["title","show"],
computed: {
        flag(){
            return localStorage.routeName === "search";
        }
    },
methods: {
    goback(){
        this.$router.go(-1)
    },
    gotoserach(){
        this.$router.push({name:"search"})
    }
}
 
}
</script>
<style>
#ysl{
    background-color: #000;
}
</style>
