<template>
    <div>
        <Head title="注册" :show="!!1" ></Head>
        
            
      
         <mt-tab-container-item>
             <h2>欢迎注册</h2>
                <mt-field label="用户名" placeholder="请输入用户名" type="text" v-model="username" @change="user"></mt-field>
                <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"  @change="pwd"></mt-field>
                 <mt-field label="生日" placeholder="请输入生日" type="date" id="date" v-model="date" name="date"></mt-field>
                <mt-field label="邮箱" placeholder="请输入邮箱" type="email" v-model="email" @change="eml"></mt-field>
                <mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model="tel" @change="telphone" ></mt-field>
                <mt-button class="register" type="primary" @click="register">注册</mt-button>
            </mt-tab-container-item>
    </div>
</template>

                <script>
                import {http} from "../utils";
                import { Toast } from 'mint-ui';


                export default{
                data(){
                    return{
                        username:"",
                        password:"",
                        email:"",
                        tel:"",
                        date:""
                    }
                },
                methods:{
                 user: function(val) {
      var reg = /^[a-z_$][0-9a-z_$]{5,9}$/gi;
      if (reg.test(val)) {
        // Toast('√');
      } else {
        Toast("请输入6位以上首位是字母后面可以是字母数字");
      }
    },
    pwd: function(val) {
      var reg = /^[a-z0-9]{6,12}/gi;
      if (reg.test(val)) {
        // Toast('√');
      } else {
        Toast("请输入6位以上的只能是数字和字母");
      }
    },
    eml: function(val) {
      var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
      if (reg.test(val)) {
      } else {
        Toast("email格式不对");
      }
    },
   userdate: function(val) {
      if (val == "") {
      }
    },
    telphone: function(val) {
      var reg = /^1[35678][0-9]{9}$/g;
      if (reg.test(val)) {
      } else {
        Toast("输入常用的手机号");
      }
    },
    register:function(){
         var usernamereg = /^[a-z_$][0-9a-z_$]{5,9}$/gi;
      if (usernamereg.test(this.username)) {
        var reg = /^[a-z0-9]{6,12}/gi;
        if (reg.test(this.password)) {
          var emailreg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/;
          if (emailreg.test(this.email)) {
            if (this.date != null) {
              var telreg = /^1[35678][0-9]{9}$/g;
              if (telreg.test(this.tel)) {
                var obj = {
                  username: this.username,
                  password: this.password,
                  tel: this.tel,
                  email: this.email,
                  
                };
                http.get("vue/zhuce",{params:obj})
                  .then(res => {
                   
                    Toast(res.data.result.msg)
                    if(res.data.result.msg=="注册成功!"){
                  
                      this.$router.push({name:"login"})
                    }
                  });
              } else {
                Toast("手机号格式有误或为空");
              }
            } else {
              Toast("生日不能为空");
            }
          } else {
            Toast("email格式有误或为空");
          }
        } else {
          Toast("密码输入有误或为空");
        }
      } else {
        Toast("用户名输入有误或为空");
      }
    }
   
    }
                }



</script>

<style scoped>
h2{
    font-size: .8rem;
    margin-bottom: .5rem;
}
.mint-tab-container-item {
    -ms-flex-negative: 0;
    flex-shrink: 0;
    width: 100%;
    margin: top;
    margin-top:2rem;
    text-align: center
}
.register{
    margin-top: .5rem
}
</style>
