<template>
<div>
<Head title="个人中心" ></Head>
<div v-if="show">
<mt-button size="large" type="danger" class="btn" @click="gotologin">登录</mt-button>
<mt-button size="large" type="primary" @click="gotoregister">注册</mt-button>
</div>
<div  class="info" v-else>
    <div class="xinxi"> 
    <h3><i class="iconfont">&#xe64c;</i>{{user.username}}</h3>
    <h3>邮箱:{{user.email}}</h3>
     <h3>手机号:{{user.tel}}</h3>    
     </div> 
     <div class="goodsxiangguan">
         <ul>
 <li>
         <span class="iconfont">&#xe60b;</span>
         <p>待付款</p>
     </li>
     <li>
         <span class="iconfont">&#xe62e;</span>
         <p>待发货</p>
     </li>
     <li>
         <span class="iconfont">&#xe62e;</span>
         <p>待收货</p>
     </li>
     <li>
         <span class="iconfont">&#xe614;</span>
         <p>退款售后</p>
     </li>
         </ul>
         <hr>
    <p class="likeme">我的喜欢</p>
    <div class="like">
        <div class="hh" @click="gotolikecar">
           
             <span class="iconfont likegoods">&#xe60a;</span>
        <p>我的收藏</p>
        </div>
       
    </div>
     
    <hr>
    <div class="like">
        <div class="hh" @click="godingdan()">
             <span class="iconfont">&#xe639;</span>
        <p>我的订单</p>
        </div>
       
    </div>
    <hr>
    <mt-button size="large" type="danger" @click="zhuxiao">注销</mt-button>
</div>
</div>
</div>

</template>


<style lang="css" scoped>
.btn{
    margin-top: 5.5rem;
    margin-bottom: .5rem;
}
</style>

<script>
import { http } from '../utils';
import { Badge } from 'mint-ui';
export default {
    
    data(){
        return{
         show:true  ,
         user:[],
         num:0
        }
    },
    
    methods: {
        godingdan(){
            this.$router.push({name:"dingdan"})
        },
        gotologin(){
            this.$router.push({name:"login"})
        },
        gotoregister(){
            this.$router.push({name:"register"})
        },
        zhuxiao(){
            localStorage.username = "";
            this.show = true;
        },
        gotolikecar(){
            this.$router.push({name:"likegoods"})
        }
    },
   mounted(){
       if(localStorage.username){
           	http.get("/vue/likegoods",{
			params:{
				username:localStorage.username
			}
		}).then(res=>{
			console.log(res);
			this.num = res.data.num;
		})
           http.get("/vue/getUserOne",{
               params:{
                   username:localStorage.username
               }
           }).then(res=>{
               this.user = res.data.result;
               console.log(this.user)
           })
           this.show = false;
       }else{
           this.show  = true;
       }
   }
    
}
</script>
<style scoped>
.xinxi{
    border-bottom: 2px solid #cccccc;
      background-color:gray;
      margin-bottom: .3rem;
      color:white;
}
ul{
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color:gray;
}
li{
    list-style: none;
    float: left;
    width: 23%;
    text-align: center;
    margin-left:2%; 
    margin-top: .5rem;
    color:white;
}
.likeme{
    font-weight: 700;
    font-size: .4rem;
    margin-bottom: .3rem;
    margin-top: .3rem;
}
.like{
    text-align: center;
   background-color: gray;
   color:white;
  height: 1.6rem;
  /* padding-bottom: .3rem; */
  overflow: hidden;
}
.hh{
    margin-top: .3rem;
    position: relative;
}
.likegoods{
    color: red;
}
.hot{
				position: absolute;
				top:0;
				left:60%;
				display: inline;
				width:.25rem;
			}
</style>
