<template>
    <div>
    <mt-loadmore 
        :top-method="loadTop" 
        :bottom-method="loadBottom"
        :bottom-all-loaded="allLoaded" 
        ref="loadmore"
        >
     <ul
     v-infinite-scroll="loadMore"
            infinite-scroll-disabled="loading"
            infinite-scroll-distance="100"
     >
         <li v-for="(good,i) in goods" :key="i" class="moveIn">
             
             <router-link :to="{name:'goodDetail',params:{goodId:good._id}}">
 <img :src="good.goodsimg">
 <p class="name">{{good.goodsname}}</p>
 <p class="price">￥:{{good.goodsprice}}RMB </p>
   
             </router-link>
            <span class="iconfont" @click.stop="shoucang()">&#xe60a;</span>
         </li>
     </ul>
        </mt-loadmore>  
    </div>
</template>


<script>
import {Toast} from "mint-ui";
import {Indicator} from "mint-ui";
import {http} from "../utils";
import { setTimeout } from 'timers';
export default {
    data(){
        return{
           goods:[],
            allLoaded:false,
            loading:false,
        }
    },
     props:["type","goodsList"],
     methods:{
           loadTop(){
            console.log("下拉刷新 --top")
            setTimeout(()=>{
                this.goods.reverse();
              
                this.$refs.loadmore.onBottomLoaded();
                Toast({
                    message:'刷新成功',
                    duration:500
                });
            },1000)
        },
        loadBottom(){
            console.log("bottom");
        },
        loadMore(){
            console.log("上拉加载更多");
            
        },
       shoucang(){
            if(localStorage.username){
                  Indicator.open({
            text: '正在收藏中...',
            spinnerType: 'triple-bounce'
        });
               http.get("/vue/gotoshoucangcar",{
                   params:{
                       goodsid:this.goods._id,
                        goodsimg:this.goods.goodsimg,
                        
                        goodsname:this.goods.goodsname,
                        goodsprice:this.goods.goodsprice,
                       username:localStorage.username
                      
                   }
               }).then(res=>{
                   console.log(res)
                   if(res.data.result.type=="1"){
                       Toast("该商品已经收藏过");
                         Indicator.close();
                   }else{
                        Indicator.close();
                     Toast("收藏成功");
                   }
                   
               })
            }else{
                 Toast("请先进行登录噢");
                this.$router.push({name:"login"});
            }
       },
    
     },

    
     mounted(){
         setTimeout(()=>{
   console.log(this.goodsList)
       this.goods = this.goodsList.filter((good)=>good.goodstype==this.type)
    console.log(this.goods)
         },2000)
        
     
     }
}
</script>


<style scoped>
ul, img{
    width: 100%;
    text-align: center;
    
}
ul{
    margin-top: 1.4rem;
      padding-bottom: 3.9rem;
      margin-bottom: 10rem;
}
.mint-loadmore{
       
      margin-bottom:.8rem;
}
li{
    width: 50%;
    float: left;
    height: 100%;
}
li{
    margin-bottom: .2rem;
}

.name{
    color: #000000;
}
.price{
    color: red;
    font-size: .4rem;
    font-weight: 700;
}
.moveIn{
    animation:bounceln 2s ;
        height: 6rem; 
}
.iconfont{
    color: aqua;
    font-size: .4rem;
}
.xiangqing{
    margin-left: .3rem;
    margin-right: .3rem;
}
</style>
