<template>
    <div>
    <Head title="分类" :show="!!1" class="head"></Head>
    <div v-if="show" class="classify">
        <mt-navbar v-model="selected">
  <mt-tab-item :id="type" v-for="(type,i) in types" :key="i">{{type}}</mt-tab-item>
</mt-navbar>
<mt-tab-container v-model="selected">
  <mt-tab-container-item :id="type" v-for="(type,i) in types" :key="i">
   <Goodlist :type="type" :goodsList="goodsList"></Goodlist>
  </mt-tab-container-item>
  
  
</mt-tab-container>
    </div>
    </div>
</template>

<script>
import { Indicator } from 'mint-ui';
import {http} from "../utils";
import { setTimeout } from 'timers';
import Goodlist from "../components/goodlist.vue"
export default {
    data(){
        return{
            show:false,
            types:[],
            selected:"彩妆",
            goodsList:[]
        }
    },
     components:{
        Goodlist
    },
    mounted(){
        Indicator.open({
      text: '加载中...',
       spinnerType: 'fading-circle'
    });
 
          http.get("/vue/getGoodList",{})

      .then(res=>{   
      
         
     
            this.goodsList = res.data.result;
           
      
             
        
     
    
      })
     
      http.get("/vue/getGoodTypes",{})
      .then(res=>{
      
          setTimeout(()=>{
      this.types = res.data.result;
       this.show=true;
       Indicator.close();

          },500)
      })
  
   
    }
}
</script>

<style scoped>
.head{
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    z-index: 9999
}
.mint-navbar{
    position: fixed;
    left:0;
    top: 0;
margin-top: .8rem;
width: 100%;
z-index:999;
}
</style>
