<template>
    <div>
        <Head title="登录" :show="!!1" ></Head>
        
        <mt-tab-container>
           
            <mt-tab-container-item id="login">
                 <h2>欢迎登录</h2>
                <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
                <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
                <mt-button  type="danger" @click="login" class="login">登录</mt-button>
            </mt-tab-container-item>
           
        </mt-tab-container>
    </div>
</template>

<script>
import { http } from '../utils';
import { Toast } from "mint-ui";
export default {
    data(){
        return {
           
            username:"",
            password:"",
          
        }
    },
    methods:{
       login() {       
      http
        .get("vue/userlogin", { params: { username: this.username } })
        .then(res => {
          if (res.data.result) {
          
            if(this.password==res.data.result.password){
              localStorage.username = this.username;
              this.$router.go(-1);
            }else{
              Toast("输入密码错误");
            }
          } else {
            Toast("用户名不存在");
          }
        });
    }
        
    }
}
</script>

<style lang="scss" scoped>
h2{
    font-size: .8rem;
    margin-bottom: .5rem;
}
.mint-tabbar.is-fixed {
    right: 0;
    top: .8rem;
    left: 0;
    position: fixed;
    z-index: 1;
    height:.6rem;
}
.mint-tab-container {
    /* overflow: hidden; */
    position: relative;
    margin-top: 2rem;
    text-align: center;
}
/deep/ element.style{
    display: block;
}
.mint-tab-container-item{
display: block !important;
}
.login{
  margin-top: .5rem;
}
</style>

