import Vue from 'vue'
import Vuex from 'vuex'
import { http } from './utils'


Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        num2: 0
    },
    actions: {
        update({ commit }, { url, id, goodsnum, cb }) {
            http.get(url, {
                params: {
                    id,
                    goodsnum
                }
            }).then(res => {
                commit("updateGood", res)
                cb()
            })
        },
        jie({ commit }, { url, list, cb }) {
            http.get(url, {
                params: {
                    list
                }


            }).then(res => {

                commit("jiesuanGood", res)
                cb();
            })
        },
        dingdan({ commit }, { url, username, goodsname, goodsimg, goodsid, goodsprice, goodsnum, goodstype, cb }) {
            http.get(url, {
                params: {
                    username,
                    goodsimg,
                    goodsid,
                    goodsname,
                    goodsprice,
                    goodsnum,
                    goodstype
                }
            }).then(res => {
                commit("gotodingdan", res)
                console.log(res.data.result);
                cb()
            })
        },
        num({ commit }, { url, username, cb }) {
            http.get(url, {
                params: {
                    username,

                }
            }).then(res => {
                commit("getGoods", res.data.num)
                cb();
            })
        }
    },
    mutations: {
        updateGood(state, msg) {
            state.msg = msg;
        },
        jiesuanGood(state, msg) {
            state.msg = msg;
        },
        getGoods(state, num2) {
            state.num2 = num2;
        },
        gotodingdan(state, msg) {
            state.msg = msg
        }
    }


})