import Vue from "vue";
import VueRouter from "vue-router";

//1
Vue.use(VueRouter); //全局声明 路由模块
//1.声明组件
import Home from "./views/home.vue";
import Mine from "./views/mine.vue";
import Guide from "./views/guide.vue";
import Index from "./views/index.vue";
import Car from "./views/car.vue";
import Classify from "./views/classify.vue";
import Search from "./views/search.vue";
import Login from "./views/login.vue";
import Register from "./views/register.vue";
import GoodDetail from "./views/gooddetail.vue"
import Likegoods from "./views/likegoods.vue"
import Dingdan from "./views/dingdan.vue"
//2.配置路由参数
const routes = [{
    path: "/",

    component: Guide,
    name: "guide"
}, {
    path: "/guide",
    component: Guide,
    name: "guide"
}, {
    path: "/app",
    component: Index,
    name: "app",
    children: [{
        path: "",
        redirect: { name: "home" }
    }, {

        path: "home",
        component: Home,
        name: "home"
    }, {
        path: "mine",
        component: Mine,
        name: "mine"
    }, {
        path: "car",
        component: Car,
        name: "car"
    }, {
        path: "classify",
        component: Classify,
        name: "classify"
    }]
}, {
    path: "/search",
    component: Search,
    name: "search"
}, {
    path: "/login",
    component: Login,
    name: "login"
}, {
    path: "/register",
    name: "register",
    component: Register
}, {
    path: "/likegoods",
    name: "likegoods",
    component: Likegoods
}, {
    path: "/dingdan",
    name: "dingdan",
    component: Dingdan
}, {
    path: "/gooddetail/:goodId?",
    name: "goodDetail",
    component: GoodDetail
}, {
    path: "*",
    redirect: { name: "home" }
}];

//.创建路由对象
const router = new VueRouter({
    routes
})

//4.挂载到根实例
export default router //默认导出