<template>
<div>
<Head title="YvesSaintLaurent"></Head>
 <mt-swipe :auto="2000" class="home">
   <mt-swipe-item v-for="(img,i) in imgs" :key="i">
                <img :src="img" alt="" class="g-img">
            </mt-swipe-item>
</mt-swipe>
<h3 class="mingxing">明星产品</h3>
<mt-navbar v-model="selected">
  <mt-tab-item :id="type" v-for="(type,i) in types" :key="i">{{type}}</mt-tab-item>
</mt-navbar>
<mt-tab-container v-model="selected">
  <mt-tab-container-item :id="type" v-for="(type,i) in types" :key="i">
   <Goodslist :type="type" :goodsList="goodsList"></Goodslist>
  </mt-tab-container-item>  
</mt-tab-container>
</div>

</template>

<script>

import {http} from "../utils";
import Goodslist from "../components/goodslist2.vue";
export default {
     data(){
        return {
           
            imgs:[
                require("../assets/images/home1.jpg"),   //把图片当做模块 
                require("../assets/images/home2.jpg"),
                require("../assets/images/home3.jpg"),
                require("../assets/images/home4.jpg"),
                 require("../assets/images/home5.jpg")
            ],
            selected:"护肤",
            show:true,
            types:[],
            goodsList:[]
            
      
        }
      
    },
     components:{
       Goodslist
    },
     mounted() {
          http.get("/vue/getGoodList",{})
      .then(res=>{   
     this.goodsList = res.data.result;
    
      }),
          http.get("/vue/getGoodTypesthree",{})
      .then(res=>{              
      this.types = res.data.result;
       this.show=true;      
      })
    
       }
                                     
                                     
}
</script>


 <style >
 .mint-swipe-items-wrap{
     height: 5.5rem;
 }
 .home{
    width:100%;
    height:100%;
}
.g-img{
    width: 100%;
  
}
.mingxing{
    text-align: center;
    font-weight: 700;
    font-size: 20px;
    margin-top: .3rem;
}
 </style>
 